import React from "react";
import "./App.scss";
import AUDIOS_LIST from "./audios";

const App = () => {
  return (
    <div className="App">
      <div id="title">
        <h1 id="title-drum">
          Dr<span className="u">u</span>m
        </h1>
        <h1 id="title-machine">Machine</h1>
      </div>
      <div id="drum-machine">
        <div id="display">
          <p className="now-playing">Now playing</p>
          <p className="audio-name" id="audio-name">
            Press a key!
          </p>
        </div>
        <div className="pads">
          {AUDIOS_LIST.map((currAudio, idx) => (
            <Pad
              text={currAudio.key}
              desc={currAudio.name}
              key={idx}
              audio={currAudio.url}
            />
          ))}
        </div>
      </div>
    </div>
  );
};

class Pad extends React.Component {
  constructor(props) {
    super(props);

    this.audio = React.createRef();
  }

  render() {
    const { text, audio, desc } = this.props;

    // Activate Pads on PRESSING (sound, CSS properties + audio-name display)
    this.playAudio = () => {
      this.audio.current.play();
      document.getElementById(text).currentTime = 0;
      document.getElementById("audio-name").textContent = desc;
    };

    return (
      <button onClick={this.playAudio} className="drum-pad" id={`drum-${text}`}>
        {text}
        <audio ref={this.audio} id={text} className="clip" src={audio} />
      </button>
    );
  }
}

// Activate Pads on KEYDOWN (sound, CSS properties + audio-name display)
document.addEventListener("keydown", (e) => {
  const id = e.key.toUpperCase();
  const audio = document.getElementById(id);

  if (audio) {
    audio.currentTime = 0;
    audio.play();
    const parent = audio.parentNode;
    parent.classList.add(`active-${id}`);

    switch (id) {
      case "Q":
        document.getElementById("audio-name").textContent = AUDIOS_LIST[0].name;
        break;
      case "W":
        document.getElementById("audio-name").textContent = AUDIOS_LIST[1].name;
        break;
      case "E":
        document.getElementById("audio-name").textContent = AUDIOS_LIST[2].name;
        break;
      case "A":
        document.getElementById("audio-name").textContent = AUDIOS_LIST[3].name;
        break;
      case "S":
        document.getElementById("audio-name").textContent = AUDIOS_LIST[4].name;
        break;
      case "D":
        document.getElementById("audio-name").textContent = AUDIOS_LIST[5].name;
        break;
      case "Z":
        document.getElementById("audio-name").textContent = AUDIOS_LIST[6].name;
        break;
      case "X":
        document.getElementById("audio-name").textContent = AUDIOS_LIST[7].name;
        break;
      case "C":
        document.getElementById("audio-name").textContent = AUDIOS_LIST[8].name;
        break;
      default:
        document.getElementById("audio-name").textContent = "Press a key!";
    }

    audio.addEventListener("ended", () => {
      parent.classList.remove(`active-${id}`);
    });
  }
});

export default App;
